.blogs {
  margin-bottom: 116px;
  
  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 65px;
    @media screen and (max-width: 600px) {
      flex-direction: column;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 800;
    font-size: 42px;
    line-height: 50px;
    margin: 0;
    color: $basic-black;
    @media screen and (max-width: 1235px) {
      font-size: 26px;
      text-align: center;
    }
  }

  &__category {
    font-size: 16px;
    line-height: 30px;
  }

  &__name {
    margin: 0;
    font-style: normal;
    font-weight: 800;
    font-size: 20px;
    line-height: 36px;
    color: $basic-black;
  }

  &__description {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    margin: 0;
  }

  &__blocks {
    display: flex;
  }

  &__left {
    display: flex;
    @media screen and (max-width: 1235px) {
      display: none;
    }
  }

  &__right {
    width: 100%;
  }

  &__item {
    margin-right: 30px;
  }

  &__name {
    padding: 11px 0 8px 0;
  }

  &__list {
    display: flex;
    padding: 26px 0;
    border-bottom: 2px solid #DEE1E6;
  }

  &__list:first-child {
    padding-top: 0;
  }

  &__list-data {
    font-size: 16px;
  }

  &__list-name {
    font-style: normal;
    font-weight: 800;
    font-size: 18px;
    color: $basic-black;
  }

  &__img {
    height: 100%;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 16px;
  }

  &__button {
    @media screen and (max-width: 600px) {
      text-align: center;
      margin: 0 auto;
      width: 200px;
    }
  }
}